
body, html {
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.drum-machine {
  display: flex;
  justify-content: space-between;
  background-color: #3b3f46;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 600px; 
  height: 300px;
  max-width: 90%;
}

.padKeys {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  width: 45%;
}

.drum-pad {
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 1.5rem;
  padding: 20px;
  cursor: pointer;
  outline: none;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  width: 80px;
  height: 80px;
}
.drum-pad:hover {
  background-color: #1b9420;
}
.drum-pad:active {
  background-color: #66bb6a;
}

.panelControl {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45%;
}

.powerButton, .soundDisplay, .volumeControl, .soundBankSelector {
  width: 100%;
  margin-bottom: 10px;
}

.powerButton {
  background-color: #ff4136;
  width:35%;
  height:30%;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.powerButton.on {
  background-color: #2ecc40;
}

.powerButton:hover {
  color:black;
}

.soundDisplay {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.volumeControl {
  display: flex;
  align-items: center;
  width: 100%;
}

.volumeControl input[type="range"] {
  flex-grow: 1;
  margin: 0 15px;
}

.volumeControl p {
  margin: 0;
  padding: 0 10px;
}

.soundBankSelector {
  background-color: #0074d9;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease;
}

.soundBankSelector:hover {
  background-color: #025ba8;
}

@media (max-width: 600px) {
  body, html {
    font-size: 50%; 
  }

  .drum-machine {
    flex-direction: column;
    width: 300px;
    height: auto;
    padding: 10px; 
  }

  .padKeys {
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
  }

  .drum-pad {
    width: calc((100% - 20px) / 3);
    height: calc((100% - 20px) / 3);
    margin: 10px;
    font-size: 2rem;
  }

  .powerButton, .soundDisplay, .volumeControl, .soundBankSelector {
    width: calc(100% - 20px);
    margin: 10px 0; 
  }

  .powerButton {
    height: calc((100% - 20px) / 4); 
    align-self: center; 
  }

  .soundDisplay, .volumeControl, .soundBankSelector {
    align-self: center;
  }
}
